
import React from "react";

// SubViews
import Pontuacao from "./SubViewComponentsJogo/tabuleiro/Pontuacao"
import Cell from "./SubViewComponentsJogo/tabuleiro/Cell"



export default class tabuleiro extends React.Component {

    constructor(props) {
        
        super(props);
    }
    

    // Linked Functions



    // End Linked Funcitons


    // View Composition Functions

    renderCell(lin,col) {
        return <Cell 
            ref={React.createRef()}
            key={col+lin*this.props.ControleJogo.ControleModo.getAltura()}
            index={col+lin*this.props.ControleJogo.ControleModo.getAltura()}
            ControleJogo={this.props.ControleJogo}
            update={this.props.update}
        />
    }

    renderRow(lin,col){
        var cellBoard = [];
        for (let index = 0; index < col; index++) {
            cellBoard.push(this.renderCell(lin,index));
        }
        let keyindex = (lin*col)+100
        return React.createElement('div', { key : keyindex, className: "d-flex" }, cellBoard);
    }


    renderBoard(lin,col) {
        var rowsBoard = [];
        for (let index = 0; index < lin; index++) {
            rowsBoard.push(this.renderRow(index,col));
        }
        return React.createElement('div', { className: "main-board container pt-2 pt-md-5 px-md-5", key:10051 }, rowsBoard);
    }

    // End View Composition Functions
   

    render() {
       
        return (
            <div className="flex-grow-1 d-flex flex-row col-md-flex align-items-center w-100 viewjogo bg-color3 arround-tl-12">
              <div  className=" d-flex w-100-sm h-100-md">
                <Pontuacao key={5000} ControleJogo={this.props.ControleJogo} update={this.props.update}  Usuario={this.props.Serve} /> 
              </div>
              <div  className="flex-grow-1 h-100 w-100 d-flex">
              {this.renderBoard(this.props.ControleJogo.ControleModo.getAltura(),this.props.ControleJogo.ControleModo.getLargura())}

              </div>
            </div>
        );
    }
}