import React from "react";

//Logos
/* eslint-disable no-unused-vars */

//Componentes de roteamento
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

/**
 * @param {Null} Null Null
 * @description Classe geradora da função app
 * @constructor
 */

export default class Vitoria extends React.Component {
  // eslint-disable-next-line no-useless-constructor
  constructor(props) {
    super(props);
    console.log("TENTANDO LER");
    console.log(props);
    this.Serve = props.Serve;
    console.log(this.props.ControleJogo.state.pontos);
    console.log(this.props.ControleJogo.state.tempo);
    this.updateProgresso();
  }

  updateProgresso() {
    console.log(this.Serve);
    // this.Serve.usuario.setProgresso(this.props.ControleJogo.modo,this.props.ControleJogo.ControleModo.getFase(),this.props.ControleJogo.state.pontos,this.props.ControleJogo.state.tempo)
    this.Serve.usuario.setProgresso(
      this.props.ControleJogo.modo,
      this.props.ControleJogo.ControleModo.getFase(),
      8000,
      { minutes: 2, seconds: 58 }
    );
  }

  render() {
    // ROTEAMENTO - colocar controle de estados da aplicação
    return (
      <div className="d-flex w-100 flex-row  min-vh-100  align-items-center p-0">
        <div className="d-flex flex-column container align-items-center w-100 view arround-12  ">
          <div className="d-flex w-100  h-100 bg-color1  arround-12 p-5">
            <div className=" text-center flex-fill row w-100  h-100 bgVitoria   d-flex flex-row bg-white1 align-items-center w-100 arround-12 ">
              <div className="col-md">
                <div className="w-100 d-flex py-2 align-items-center">
                  <p className=" vitoria p-3 mx-5 flex-fill align-self-center bg-color1 w-fit arround-12">
                    PARABÉNS!
                  </p>
                </div>
                <div className="flex-fill d-flex justify-content-evenly  flex-row p-md-5">
                  <div className="d-flex flex-row visor bg-color1 ">
                    <div className="tag text-white1 flex-grow-1 align-text-bottom">
                      Cronômetro
                    </div>
                    <div
                      id="time_value"
                      className="value bg-white1 text-color1"
                    >
                      {(
                        "00" + this.props.ControleJogo.state.tempo.minutes
                      ).slice(-2)}
                      :
                      {(
                        "00" + this.props.ControleJogo.state.tempo.seconds
                      ).slice(-2)}
                    </div>{" "}
                  </div>
                  <div className="d-flex flex-row visor bg-color1 ">
                    <div className="tag text-white1 flex-grow-1 align-text-bottom">
                      Pontuação
                    </div>
                    <div className="value bg-white1 text-color1">
                      {this.props.ControleJogo.state.pontos < 0
                        ? "-" +
                          (
                            "0000" +
                            this.props.ControleJogo.state.pontos * -1
                          ).slice(-4)
                        : ("0000" + this.props.ControleJogo.state.pontos).slice(
                            -4
                          )}
                    </div>
                  </div>
                </div>
                <p>
                  {" "}
                  Você concluiu o tema{" "}
                  {this.props.ControleJogo.ControleModo.nivel} nível{" "}
                  {this.props.ControleJogo.ControleModo.getFase()}!
                </p>
              </div>

              <div className="col-md">
                <p>
                  Aprenda ainda mais enquanto se diverte! <br /> Desafie-se e
                  jogue novamente
                </p>

                <Link to="/modo">
                  <button
                    type="button"
                    className="button-custom inicial bg-color4 arround-6"
                  >
                    <span className="material-icons-outlined md-44 align-middle pe-3">
                      sports_esports
                    </span>
                    JOGAR
                  </button>
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
