import React from "react";
import Timer from "./Timer";
// Controladores

export default class Cell extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {}

  componentWillUnmount() {}

  progresso( fase, estrela){
    let pontos =  this.props.Usuario.usuario.estrelas[fase][estrela]
    if(pontos >= this.props.ControleJogo.state.pontos)
      return "star_outline"
    else 
      return "star"
  }

  fracaoprogresso(){
    let pontos = this.props.ControleJogo.state.pontos
    let maximo = this.props.Usuario.usuario.estrelas[this.props.ControleJogo.ControleModo.getFase()][3]
    return (pontos/maximo)*100
  }

  render() {
    return (
      <div className="flex-fill d-flex col-sd-flex flex-column p-md-5">
        <div className="d-flex flex-row visor bg-white1 ">
          <div className="tag text-color1 flex-grow-1 align-text-bottom">
            Cronômetro
          </div>
          <Timer
            ControleJogo={this.props.ControleJogo}
            update={this.props.update}
            Usuario={this.props.Serve}
          />
        </div>
        <div className="d-flex flex-row visor bg-color1 mt-md-5">
          <div className="star-contain">
          <div className="material-icons-outlined  align-middle">
            {this.progresso(this.props.ControleJogo.ControleModo.getFase(),1)}
          </div>
          <div className="material-icons-outlined  align-middle">
            {this.progresso(this.props.ControleJogo.ControleModo.getFase(),2)}
          </div>
          <div className="material-icons-outlined  align-middle">
            {this.progresso(this.props.ControleJogo.ControleModo.getFase(),3)}
          </div>
          </div>
        <div className="progress star-progress1 w-100">
        
        <div className="progress-bar progress-bar-striped progress-bar-animated star-progress" role="progressbar" aria-valuenow={this.fracaoprogresso()} aria-valuemin="0" aria-valuemax="100" style={{width: this.fracaoprogresso()}}></div>

        </div>
        </div>
        <div className="d-flex flex-row visor bg-white1 mt-md-5">
          <div className="tag text-color1 flex-grow-1 align-text-bottom">
            Pontuação
          </div>
          <div className="value bg-color1 text-white1">
            {this.props.ControleJogo.state.pontos < 0
              ? "-" +
                ("0000" + this.props.ControleJogo.state.pontos * -1).slice(-4)
              : ("0000" + this.props.ControleJogo.state.pontos).slice(-4)}
          </div>
        </div>
        
      </div>
    );
  }
}
