import React from 'react';
export default class Timer extends React.Component {
    constructor(props) {
        super(props);
        this.state = { seconds: 0 , minutes: 0 };
    }

    tick() {
        if (this.state.seconds === 59) {
            this.setState(state => ({
                minutes: state.minutes + 1
            }));
            this.setState(state => ({
                seconds: 0
            }));
        } else {
            this.setState(state => ({
                seconds: state.seconds + 1
            }));
        }
        this.updateProgressoTempo(this.state)
    }

    componentDidMount() {
        this.interval = setInterval(() => this.tick(), 1000);
    }

    componentWillUnmount() {
        clearInterval(this.interval);
    }

    updateProgressoTempo(tempo){
        // console.log("Controle jogo",this.props.ControleJogo.state.tempo);
        this.props.ControleJogo.updateProgressoTempo(tempo)
        this.props.update();
    }

    render() {
        return (
            <div id='time_value' className='value bg-color1 text-white1'>
                {('00' + this.state.minutes).slice(-2)}:{('00' + this.state.seconds).slice(-2)}
            </div>
        );
    }
}